*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,body{
  background-color: #efe8e9;
}
h3{
  text-align: center;
  padding: 18px 0;
  font-size: 30px;
}
.form-group{
  text-align: center;
}
p{
  padding: 10px 20px;
  font-size: 16px;
}
input[type=number]{
  width: 130px;
}
input[type=number]:focus{
  border:none;
  outline-color: purple;
}
button:focus {
  outline: none;
  border: none;
}